"use client";

import { Button } from "@/components/ui/button";
import { Loader2, Navigation } from "lucide-react";
import { useState } from "react";

interface GeoLocationButtonProps {
  variant?: "outline" | "default" | "secondary";
  size?: "icon" | "default" | "sm" | "lg";
  className?: string;
  buttonText?: string;
  iconSize?: number;
  iconColor?: string;
}

export default function GeoLocationButton({
  variant = "outline",
  size = "default",
  className = "",
  buttonText = "–ú–æ—ë –º–µ—Å—Ç–æ–ø–æ–ª–æ–∂–µ–Ω–∏–µ",
  iconSize = 8,
  iconColor = "text-cyan-500"
}: GeoLocationButtonProps) {
  const [isLoading, setIsLoading] = useState(false);

  const handleGeolocation = () => {
    if (!('geolocation' in navigator)) {
      alert("–ì–µ–æ–ª–æ–∫–∞—Ü–∏—è –Ω–µ –ø–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç—Å—è –≤–∞—à–∏–º –±—Ä–∞—É–∑–µ—Ä–æ–º");
      return;
    }

    setIsLoading(true);

    navigator.geolocation.getCurrentPosition(
      (position) => {
        const { latitude, longitude } = position.coords;
        window.location.href = `/api/weather/current/coordinates?lat=${latitude.toFixed(4)}&lon=${longitude.toFixed(4)}&redirect=true`;
      },
      (error) => {
        console.error("–û—à–∏–±–∫–∞ –≥–µ–æ–ª–æ–∫–∞—Ü–∏–∏:", error);
        let errorMessage = "–ù–µ —É–¥–∞–ª–æ—Å—å –æ–ø—Ä–µ–¥–µ–ª–∏—Ç—å –º–µ—Å—Ç–æ–ø–æ–ª–æ–∂–µ–Ω–∏–µ.";

        if (error.code === 1) {
          errorMessage = "–î–æ—Å—Ç—É–ø –∫ –≥–µ–æ–ª–æ–∫–∞—Ü–∏–∏ –∑–∞–ø—Ä–µ—â–µ–Ω. –ü–æ–∂–∞–ª—É–π—Å—Ç–∞, —Ä–∞–∑—Ä–µ—à–∏—Ç–µ –¥–æ—Å—Ç—É–ø –∫ –≤–∞—à–µ–º—É –º–µ—Å—Ç–æ–ø–æ–ª–æ–∂–µ–Ω–∏—é.";
        } else if (error.code === 2) {
          errorMessage = "–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ –º–µ—Å—Ç–æ–ø–æ–ª–æ–∂–µ–Ω–∏–∏ –Ω–µ–¥–æ—Å—Ç—É–ø–Ω–∞.";
        } else if (error.code === 3) {
          errorMessage = "–í—Ä–µ–º—è –æ–∂–∏–¥–∞–Ω–∏—è –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è –º–µ—Å—Ç–æ–ø–æ–ª–æ–∂–µ–Ω–∏—è –∏—Å—Ç–µ–∫–ª–æ.";
        }

        alert(errorMessage);
        setIsLoading(false);
      },
      { enableHighAccuracy: true, timeout: 10000, maximumAge: 0 }
    );
  };

  return (
    <Button
      variant={variant}
      size={size}
      className={className}
      onClick={handleGeolocation}
      disabled={isLoading}
    >
      {isLoading ? (
        <Loader2 className={`h-${iconSize} w-${iconSize} animate-spin`} />
      ) : (
        <Navigation className={`h-${iconSize} w-${iconSize} ${iconColor}`} />
      )}
      {buttonText && <span>{buttonText}</span>}
    </Button>
  );
}
