"use client";

import { useState, useEffect } from 'react';
import Link from 'next/link';
import { Heart, X, MapPin, RefreshCw } from 'lucide-react';
import { Card, CardContent, CardHeader, CardTitle } from './ui/card';
import { Button } from './ui/button';
import { Badge } from './ui/badge';
import {
  getFavoriteCities,
  removeFromFavorites,
  sortFavoriteCities,
  type FavoriteCity
} from '@/lib/favorites';

interface FavoriteCitiesProps {
  className?: string;
  limit?: number;
  onSelect?: (city: FavoriteCity) => void;
}

export default function FavoriteCities({ className, limit = 5, onSelect }: FavoriteCitiesProps) {
  const [favorites, setFavorites] = useState<FavoriteCity[]>([]);
  const [sortBy, setSortBy] = useState<'name' | 'addedAt'>('addedAt');
  const [sortOrder, setSortOrder] = useState<'asc' | 'desc'>('desc');

  // –ó–∞–≥—Ä—É–∂–∞–µ–º –∏–∑–±—Ä–∞–Ω–Ω—ã–µ –≥–æ—Ä–æ–¥–∞ –∏–∑ localStorage
  useEffect(() => {
    const favorites = getFavoriteCities();
    const sortedFavorites = sortFavoriteCities(favorites, sortBy, sortOrder);
    setFavorites(sortedFavorites.slice(0, limit));
  }, [limit, sortBy, sortOrder]);

  // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ —É–¥–∞–ª–µ–Ω–∏—è –≥–æ—Ä–æ–¥–∞ –∏–∑ –∏–∑–±—Ä–∞–Ω–Ω–æ–≥–æ
  const handleRemove = (cityId: string) => {
    const updatedFavorites = removeFromFavorites(cityId);
    const sortedFavorites = sortFavoriteCities(updatedFavorites, sortBy, sortOrder);
    setFavorites(sortedFavorites.slice(0, limit));
  };

  // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∏–∑–º–µ–Ω–µ–Ω–∏—è —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏
  const handleSortChange = (newSortBy: 'name' | 'addedAt') => {
    if (newSortBy === sortBy) {
      // –ï—Å–ª–∏ —Ç–æ—Ç –∂–µ —Å—Ç–æ–ª–±–µ—Ü, –∏–Ω–≤–µ—Ä—Ç–∏—Ä—É–µ–º –ø–æ—Ä—è–¥–æ–∫ —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏
      setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc');
    } else {
      // –ï—Å–ª–∏ –¥—Ä—É–≥–æ–π —Å—Ç–æ–ª–±–µ—Ü, —É—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –µ–≥–æ –∏ —Å–±—Ä–∞—Å—ã–≤–∞–µ–º –ø–æ—Ä—è–¥–æ–∫ –Ω–∞ –∑–Ω–∞—á–µ–Ω–∏–µ –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é
      setSortBy(newSortBy);
      setSortOrder(newSortBy === 'name' ? 'asc' : 'desc');
    }
  };

  return (
    <Card className={`overflow-hidden ${className || ''}`}>
      <CardHeader className="bg-muted/30 p-4">
        <CardTitle className="flex items-center text-lg">
          <Heart className="w-5 h-5 mr-2 text-primary" />
          –ò–∑–±—Ä–∞–Ω–Ω—ã–µ –≥–æ—Ä–æ–¥–∞

          <div className="ml-auto flex items-center gap-2">
            <Button
              variant="ghost"
              size="sm"
              className="h-8 text-xs"
              onClick={() => handleSortChange('name')}
            >
              <span className={sortBy === 'name' ? 'font-bold' : ''}>
                –ü–æ –∏–º–µ–Ω–∏ {sortBy === 'name' && (sortOrder === 'asc' ? '‚Üë' : '‚Üì')}
              </span>
            </Button>
            <Button
              variant="ghost"
              size="sm"
              className="h-8 text-xs"
              onClick={() => handleSortChange('addedAt')}
            >
              <span className={sortBy === 'addedAt' ? 'font-bold' : ''}>
                –ü–æ –¥–∞—Ç–µ {sortBy === 'addedAt' && (sortOrder === 'asc' ? '‚Üë' : '‚Üì')}
              </span>
            </Button>
          </div>
        </CardTitle>
      </CardHeader>

      <CardContent className="p-0">
        {favorites.length === 0 ? (
          <div className="p-4 text-center text-muted-foreground">
            <p>–£ –≤–∞—Å –ø–æ–∫–∞ –Ω–µ—Ç –∏–∑–±—Ä–∞–Ω–Ω—ã—Ö –≥–æ—Ä–æ–¥–æ–≤</p>
            <p className="text-sm mt-2">–î–æ–±–∞–≤–ª—è–π—Ç–µ –≥–æ—Ä–æ–¥–∞ –≤ –∏–∑–±—Ä–∞–Ω–Ω–æ–µ, —á—Ç–æ–±—ã –±—ã—Å—Ç—Ä–æ –ø–æ–ª—É—á–∞—Ç—å –ø—Ä–æ–≥–Ω–æ–∑ –ø–æ–≥–æ–¥—ã</p>
          </div>
        ) : (
          <ul className="divide-y">
            {favorites.map((city) => (
              <li key={city.id} className="p-3 hover:bg-muted/20 transition-colors">
                <div className="flex items-center justify-between">
                  <div className="flex-1">
                    {onSelect ? (
                      <button
                        onClick={() => onSelect(city)}
                        className="flex items-center text-left hover:text-primary transition-colors"
                      >
                        <MapPin className="w-4 h-4 mr-2 opacity-70" />
                        <span className="font-medium">{city.name}</span>
                      </button>
                    ) : (
                      <Link
                        href={`/weather/${city.id}`}
                        className="flex items-center hover:text-primary transition-colors"
                      >
                        <MapPin className="w-4 h-4 mr-2 opacity-70" />
                        <span className="font-medium">{city.name}</span>
                      </Link>
                    )}
                    <div className="flex gap-1 mt-1 ml-6">
                      <Badge variant="outline" className="text-xs">
                        {city.country}
                      </Badge>
                      {city.region && (
                        <Badge variant="outline" className="text-xs">
                          {city.region}
                        </Badge>
                      )}
                    </div>
                  </div>

                  <Button
                    variant="ghost"
                    size="icon"
                    className="h-8 w-8 text-muted-foreground hover:text-destructive"
                    onClick={() => handleRemove(city.id)}
                    title="–£–¥–∞–ª–∏—Ç—å –∏–∑ –∏–∑–±—Ä–∞–Ω–Ω–æ–≥–æ"
                  >
                    <X className="w-4 h-4" />
                  </Button>
                </div>
              </li>
            ))}
          </ul>
        )}

        {favorites.length > 0 && (
          <div className="p-3 bg-muted/10 text-center">
            <Button
              variant="ghost"
              size="sm"
              className="text-xs"
              onClick={() => {
                const allFavorites = getFavoriteCities();
                const sortedFavorites = sortFavoriteCities(allFavorites, sortBy, sortOrder);
                setFavorites(sortedFavorites.slice(0, limit));
              }}
            >
              <RefreshCw className="w-3 h-3 mr-1" />
              –û–±–Ω–æ–≤–∏—Ç—å —Å–ø–∏—Å–æ–∫
            </Button>
          </div>
        )}
      </CardContent>
    </Card>
  );
}
