"use client";

import { useState, useEffect, useCallback } from "react";

/**
 * –•—É–∫ –¥–ª—è —Ä–∞–±–æ—Ç—ã —Å localStorage —Å —Ç–∏–ø–∏–∑–∞—Ü–∏–µ–π
 * @param key –ö–ª—é—á –¥–ª—è —Ö—Ä–∞–Ω–µ–Ω–∏—è –∑–Ω–∞—á–µ–Ω–∏—è
 * @param initialValue –ù–∞—á–∞–ª—å–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ
 * @returns [value, setValue] - —Ç—å—é–ø–ª —Å–æ –∑–Ω–∞—á–µ–Ω–∏–µ–º –∏ —Ñ—É–Ω–∫—Ü–∏–µ–π –∏–∑–º–µ–Ω–µ–Ω–∏—è
 */
export function useLocalStorage<T>(key: string, initialValue: T): [T, (value: T) => void] {
  // –ü–æ–ª—É—á–∞–µ–º –∑–Ω–∞—á–µ–Ω–∏–µ –∏–∑ localStorage –∏–ª–∏ –∏—Å–ø–æ–ª—å–∑—É–µ–º –Ω–∞—á–∞–ª—å–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ
  const readValue = useCallback((): T => {
    // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –¥–æ—Å—Ç—É–ø–µ–Ω –ª–∏ localStorage
    if (typeof window === "undefined") {
      return initialValue;
    }

    try {
      const item = window.localStorage.getItem(key);
      return item ? (JSON.parse(item) as T) : initialValue;
    } catch (error) {
      console.warn(`–û—à–∏–±–∫–∞ —á—Ç–µ–Ω–∏—è localStorage –∫–ª—é—á–∞ "${key}":`, error);
      return initialValue;
    }
  }, [initialValue, key]);

  // –°–æ—Å—Ç–æ—è–Ω–∏–µ –¥–ª—è —Ö—Ä–∞–Ω–µ–Ω–∏—è —Ç–µ–∫—É—â–µ–≥–æ –∑–Ω–∞—á–µ–Ω–∏—è
  const [storedValue, setStoredValue] = useState<T>(initialValue);

  // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ–º —Å–æ—Å—Ç–æ—è–Ω–∏–µ –∏–∑ localStorage –ø—Ä–∏ –º–æ–Ω—Ç–∏—Ä–æ–≤–∞–Ω–∏–∏
  useEffect(() => {
    setStoredValue(readValue());
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  // –í–æ–∑–≤—Ä–∞—â–∞–µ–º —Ñ—É–Ω–∫—Ü–∏—é, –∫–æ—Ç–æ—Ä–∞—è –æ–±–Ω–æ–≤–ª—è–µ—Ç —Å–æ—Å—Ç–æ—è–Ω–∏–µ –∏ localStorage
  const setValue = useCallback(
    (value: T) => {
      // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –¥–æ—Å—Ç—É–ø–µ–Ω –ª–∏ localStorage
      if (typeof window === "undefined") {
        console.warn(`–ù–µ —É–¥–∞–ª–æ—Å—å —Å–æ—Ö—Ä–∞–Ω–∏—Ç—å "${key}" –≤ localStorage, window is undefined.`);
        return;
      }

      try {
        // –°–æ—Ö—Ä–∞–Ω—è–µ–º –≤ —Å–æ—Å—Ç–æ—è–Ω–∏–µ
        setStoredValue(value);

        // –°–æ—Ö—Ä–∞–Ω—è–µ–º –≤ localStorage
        window.localStorage.setItem(key, JSON.stringify(value));

        // –í—ã–∑—ã–≤–∞–µ–º —Å–æ–±—ã—Ç–∏–µ –∏–∑–º–µ–Ω–µ–Ω–∏—è localStorage, —á—Ç–æ–±—ã —É–≤–µ–¥–æ–º–∏—Ç—å –¥—Ä—É–≥–∏–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—ã
        window.dispatchEvent(new Event("local-storage"));
      } catch (error) {
        console.warn(`–û—à–∏–±–∫–∞ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è "${key}" –≤ localStorage:`, error);
      }
    },
    [key]
  );

  // –°–∏–Ω—Ö—Ä–æ–Ω–∏–∑–∏—Ä—É–µ–º –∑–Ω–∞—á–µ–Ω–∏–µ –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ localStorage –≤ –¥—Ä—É–≥–∏—Ö –≤–∫–ª–∞–¥–∫–∞—Ö
  useEffect(() => {
    const handleStorageChange = () => {
      setStoredValue(readValue());
    };

    // –ü—Ä–æ—Å–ª—É—à–∏–≤–∞–µ–º —Å–æ–±—ã—Ç–∏–µ storage
    window.addEventListener("storage", handleStorageChange);
    window.addEventListener("local-storage", handleStorageChange);

    return () => {
      window.removeEventListener("storage", handleStorageChange);
      window.removeEventListener("local-storage", handleStorageChange);
    };
  }, [readValue]);

  return [storedValue, setValue];
}

/**
 * –•—É–∫ –¥–ª—è –æ—Ç—Å–ª–µ–∂–∏–≤–∞–Ω–∏—è —Å–æ—Å—Ç–æ—è–Ω–∏—è –º–µ–¥–∏–∞-–∑–∞–ø—Ä–æ—Å–∞
 * @param query –ú–µ–¥–∏–∞-–∑–∞–ø—Ä–æ—Å, –Ω–∞–ø—Ä–∏–º–µ—Ä "(max-width: 768px)"
 * @returns boolean - —Ä–µ–∑—É–ª—å—Ç–∞—Ç –º–µ–¥–∏–∞-–∑–∞–ø—Ä–æ—Å–∞
 */
export function useMediaQuery(query: string): boolean {
  const [matches, setMatches] = useState(false);

  useEffect(() => {
    if (typeof window === "undefined") return;

    const media = window.matchMedia(query);
    setMatches(media.matches);

    const listener = (e: MediaQueryListEvent) => {
      setMatches(e.matches);
    };

    media.addEventListener("change", listener);
    return () => media.removeEventListener("change", listener);
  }, [query]);

  return matches;
}

/**
 * –•—É–∫ –¥–ª—è –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
 * @returns Object —Å –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–µ–π –æ–± —É—Å—Ç—Ä–æ–π—Å—Ç–≤–µ
 */
export function useDevice() {
  const isMobile = useMediaQuery("(max-width: 640px)");
  const isTablet = useMediaQuery("(min-width: 641px) and (max-width: 1024px)");
  const isDesktop = useMediaQuery("(min-width: 1025px)");

  return { isMobile, isTablet, isDesktop };
}

/**
 * –•—É–∫ –¥–ª—è –æ–±–Ω–∞—Ä—É–∂–µ–Ω–∏—è —Ä–µ–∂–∏–º–∞ —Ç–µ–º–Ω–æ–π —Ç–µ–º—ã —É –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
 * @returns boolean - –∏—Å–ø–æ–ª—å–∑—É–µ—Ç –ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å —Ç–µ–º–Ω—É—é —Ç–µ–º—É
 */
export function usePrefersDarkMode(): boolean {
  return useMediaQuery("(prefers-color-scheme: dark)");
}
