"use client";

import { useState, useEffect, useCallback } from 'react';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Card } from '@/components/ui/card';
import { Search, Loader2, MapPin, Navigation, CircleAlert } from 'lucide-react';
import api from '@/lib/api';
import type { City } from '@/types';
import { POPULAR_CITIES } from '@/config/constants';
import { useRouter } from 'next/navigation';

export interface CitySearchProps {
  onSelectCity?: (city: City) => void;
  minimal?: boolean;
}

export default function CitySearch({ onSelectCity, minimal = false }: CitySearchProps) {
  const [query, setQuery] = useState('');
  const [cities, setCities] = useState<City[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [isOpen, setIsOpen] = useState(false);
  const [isGeolocating, setIsGeolocating] = useState(false);
  const router = useRouter();

  const searchCities = useCallback(async (searchQuery: string) => {
    if (!searchQuery.trim()) {
      setCities([]);
      return;
    }

    setIsLoading(true);
    setError(null);

    try {
      const results = await api.searchCity(searchQuery);
      setCities(results);
    } catch (err) {
      console.error('–û—à–∏–±–∫–∞ –ø–æ–∏—Å–∫–∞ –≥–æ—Ä–æ–¥–∞:', err);
      setError('–ù–µ —É–¥–∞–ª–æ—Å—å –≤—ã–ø–æ–ª–Ω–∏—Ç—å –ø–æ–∏—Å–∫. –ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –ø–æ–ø—Ä–æ–±—É–π—Ç–µ –ø–æ–∑–∂–µ.');
      setCities([]);
    } finally {
      setIsLoading(false);
    }
  }, []);

  // Debounce search
  useEffect(() => {
    const timer = setTimeout(() => {
      if (query.trim()) {
        searchCities(query);
      }
    }, 300);

    return () => clearTimeout(timer);
  }, [query, searchCities]);

  const handleCitySelect = (city: City) => {
    setQuery('');
    setCities([]);
    setIsOpen(false);

    if (onSelectCity) {
      onSelectCity(city);
    } else {
      router.push(`/weather/${city.id}`);
    }
  };

  // –ó–∞–∫—Ä—ã–≤–∞–µ–º –≤—ã–ø–∞–¥–∞—é—â–µ–µ –º–µ–Ω—é –ø—Ä–∏ –∫–ª–∏–∫–µ –≤–Ω–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      const target = event.target as HTMLElement;
      if (!target.closest('.city-search-container')) {
        setIsOpen(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, []);

  // –§—É–Ω–∫—Ü–∏—è –¥–ª—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –ø–æ–ª—è –ø–æ–∏—Å–∫–∞
  const handleSearchFocus = () => {
    setIsOpen(true);
  };

  // –§—É–Ω–∫—Ü–∏—è –¥–ª—è –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è –º–µ—Å—Ç–æ–ø–æ–ª–æ–∂–µ–Ω–∏—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
  const handleGeolocation = useCallback(() => {
    if (!('geolocation' in navigator)) {
      setError('–ì–µ–æ–ª–æ–∫–∞—Ü–∏—è –Ω–µ –ø–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç—Å—è –≤–∞—à–∏–º –±—Ä–∞—É–∑–µ—Ä–æ–º');
      return;
    }

    setIsGeolocating(true);
    setError(null);

    navigator.geolocation.getCurrentPosition(
      async (position) => {
        const { latitude, longitude } = position.coords;

        try {
          const weatherData = await api.getWeatherByCoordinates(latitude, longitude);

          if (weatherData && weatherData.id) {
            const city: City = {
              id: weatherData.id,
              name: weatherData.name,
              country: weatherData.country,
              lat: latitude,
              lon: longitude
            };

            handleCitySelect(city);
          } else {
            setError('–ù–µ —É–¥–∞–ª–æ—Å—å –ø–æ–ª—É—á–∏—Ç—å –¥–∞–Ω–Ω—ã–µ –æ –ø–æ–≥–æ–¥–µ –¥–ª—è —Ç–µ–∫—É—â–µ–≥–æ –º–µ—Å—Ç–æ–ø–æ–ª–æ–∂–µ–Ω–∏—è');
          }
        } catch (err) {
          console.error('–û—à–∏–±–∫–∞ –ø–æ–ª—É—á–µ–Ω–∏—è –ø–æ–≥–æ–¥—ã –ø–æ –∫–æ–æ—Ä–¥–∏–Ω–∞—Ç–∞–º:', err);
          setError('–ù–µ —É–¥–∞–ª–æ—Å—å –æ–ø—Ä–µ–¥–µ–ª–∏—Ç—å –≤–∞—à–µ –º–µ—Å—Ç–æ–ø–æ–ª–æ–∂–µ–Ω–∏–µ. –ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –ø–æ–ø—Ä–æ–±—É–π—Ç–µ –ø–æ–∑–∂–µ.');
        } finally {
          setIsGeolocating(false);
        }
      },
      (error) => {
        console.error('–û—à–∏–±–∫–∞ –≥–µ–æ–ª–æ–∫–∞—Ü–∏–∏:', error);
        let errorMessage = '–ù–µ —É–¥–∞–ª–æ—Å—å –æ–ø—Ä–µ–¥–µ–ª–∏—Ç—å –≤–∞—à–µ –º–µ—Å—Ç–æ–ø–æ–ª–æ–∂–µ–Ω–∏–µ.';

        if (error.code === 1) {
          errorMessage = '–î–æ—Å—Ç—É–ø –∫ –≥–µ–æ–ª–æ–∫–∞—Ü–∏–∏ –∑–∞–ø—Ä–µ—â–µ–Ω. –ü–æ–∂–∞–ª—É–π—Å—Ç–∞, —Ä–∞–∑—Ä–µ—à–∏—Ç–µ –¥–æ—Å—Ç—É–ø –∫ –≤–∞—à–µ–º—É –º–µ—Å—Ç–æ–ø–æ–ª–æ–∂–µ–Ω–∏—é.';
        } else if (error.code === 2) {
          errorMessage = '–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ –º–µ—Å—Ç–æ–ø–æ–ª–æ–∂–µ–Ω–∏–∏ –Ω–µ–¥–æ—Å—Ç—É–ø–Ω–∞.';
        } else if (error.code === 3) {
          errorMessage = '–í—Ä–µ–º—è –æ–∂–∏–¥–∞–Ω–∏—è –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è –º–µ—Å—Ç–æ–ø–æ–ª–æ–∂–µ–Ω–∏—è –∏—Å—Ç–µ–∫–ª–æ.';
        }

        setError(errorMessage);
        setIsGeolocating(false);
      },
      { enableHighAccuracy: true, timeout: 10000, maximumAge: 0 }
    );
  }, []);

  return (
    <div className={`city-search-container relative ${minimal ? 'w-full' : 'w-full max-w-md mx-auto'}`}>
      <div className="relative">
        {minimal ? (
          <div className="flex items-center">
            <Input
              type="text"
              placeholder="–ü–æ–∏—Å–∫ –≥–æ—Ä–æ–¥–∞..."
              value={query}
              onChange={(e) => setQuery(e.target.value)}
              onFocus={handleSearchFocus}
              className={`rounded-full h-8 pl-8 ${isOpen ? 'w-full' : ''} transition-all`}
            />
            <div className="absolute left-2.5 top-1/2 transform -translate-y-1/2">
              {isLoading || isGeolocating ? (
                <Loader2 className="h-4 w-4 animate-spin text-muted-foreground" />
              ) : (
                <MapPin className="h-4 w-4 text-muted-foreground" />
              )}
            </div>
            <Button
              variant="ghost"
              size="icon"
              className="h-8 w-8 ml-1"
              onClick={handleGeolocation}
              disabled={isGeolocating || isLoading}
              title="–û–ø—Ä–µ–¥–µ–ª–∏—Ç—å –º–æ—ë –º–µ—Å—Ç–æ–ø–æ–ª–æ–∂–µ–Ω–∏–µ"
            >
              <Navigation className="h-4 w-4 text-muted-foreground hover:text-primary" />
            </Button>
          </div>
        ) : (
          <div className="flex items-center gap-2">
            <div className="relative flex-1">
              <Input
                type="text"
                placeholder="–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ –≥–æ—Ä–æ–¥–∞..."
                value={query}
                onChange={(e) => setQuery(e.target.value)}
                onFocus={handleSearchFocus}
                className="pr-10"
              />
              <div className="absolute right-3 top-1/2 transform -translate-y-1/2">
                {isLoading ? (
                  <Loader2 className="h-5 w-5 animate-spin text-muted-foreground" />
                ) : (
                  <Search className="h-5 w-5 text-muted-foreground" />
                )}
              </div>
            </div>
            <Button
              variant="outline"
              size="icon"
              className="h-10 w-10 flex-shrink-0 transition-colors hover:bg-blue-50 dark:hover:bg-blue-950/20"
              onClick={handleGeolocation}
              disabled={isGeolocating || isLoading}
              title="–û–ø—Ä–µ–¥–µ–ª–∏—Ç—å –º–æ—ë –º–µ—Å—Ç–æ–ø–æ–ª–æ–∂–µ–Ω–∏–µ"
            >
              {isGeolocating ? (
                <Loader2 className="h-5 w-5 animate-spin" />
              ) : (
                <Navigation className="h-5 w-5" />
              )}
            </Button>
          </div>
        )}
      </div>

      {error && (
        <div className="mt-2 text-sm text-red-500 flex items-center gap-1.5 animate-fadeIn">
          <CircleAlert className="h-4 w-4" />
          <span>{error}</span>
        </div>
      )}

      {isOpen && (
        <>
          {cities.length > 0 && (
            <Card className={`${minimal ? 'absolute z-50 w-full' : 'mt-2'} p-2 max-h-60 overflow-y-auto animate-fadeIn`}>
              <ul className="space-y-1">
                {cities.map((city) => (
                  <li key={city.id}>
                    <Button
                      variant="ghost"
                      className="w-full justify-start text-left font-normal group"
                      onClick={() => handleCitySelect(city)}
                    >
                      <MapPin className="h-4 w-4 mr-2 text-muted-foreground group-hover:text-primary transition-colors" />
                      {city.name}
                      {city.region && <span className="text-muted-foreground ml-1 text-sm">({city.region})</span>}
                    </Button>
                  </li>
                ))}
              </ul>
            </Card>
          )}

          {!query && !cities.length && !minimal && (
            <div className="mt-4 animate-fadeIn">
              <h3 className="text-sm font-medium mb-2">–ü–æ–ø—É–ª—è—Ä–Ω—ã–µ –≥–æ—Ä–æ–¥–∞:</h3>
              <div className="flex flex-wrap gap-2">
                {POPULAR_CITIES.map((city) => (
                  <Button
                    key={city.id}
                    variant="outline"
                    size="sm"
                    className="group"
                    onClick={() => handleCitySelect(city)}
                  >
                    <MapPin className="h-3.5 w-3.5 mr-1 group-hover:text-primary transition-colors" />
                    {city.name}
                  </Button>
                ))}
              </div>
            </div>
          )}
        </>
      )}
    </div>
  );
}
