/**
 * –ò–Ω—Ç–µ—Ä—Ñ–µ–π—Å –¥–ª—è –¥–∞–Ω–Ω—ã—Ö –æ –≥–æ—Ä–æ–¥–µ
 */
export interface FavoriteCity {
  id: string;        // ID –≥–æ—Ä–æ–¥–∞ –≤ —Ñ–æ—Ä–º–∞—Ç–µ lat{—à–∏—Ä–æ—Ç–∞}_lon{–¥–æ–ª–≥–æ—Ç–∞}
  name: string;      // –ù–∞–∑–≤–∞–Ω–∏–µ –≥–æ—Ä–æ–¥–∞
  country: string;   // –ö–æ–¥ —Å—Ç—Ä–∞–Ω—ã
  region?: string;   // –†–µ–≥–∏–æ–Ω/–æ–±–ª–∞—Å—Ç—å (–æ–ø—Ü–∏–æ–Ω–∞–ª—å–Ω–æ)
  latitude: number;  // –®–∏—Ä–æ—Ç–∞
  longitude: number; // –î–æ–ª–≥–æ—Ç–∞
  addedAt: number;   // –í—Ä–µ–º–µ–Ω–Ω–∞—è –º–µ—Ç–∫–∞ –¥–æ–±–∞–≤–ª–µ–Ω–∏—è
}

/**
 * –ö–ª—é—á –¥–ª—è —Ö—Ä–∞–Ω–µ–Ω–∏—è –≤ localStorage
 */
const FAVORITES_STORAGE_KEY = 'weatherwear_favorite_cities';

/**
 * –ü–æ–ª—É—á–∏—Ç—å —Å–ø–∏—Å–æ–∫ –∏–∑–±—Ä–∞–Ω–Ω—ã—Ö –≥–æ—Ä–æ–¥–æ–≤ –∏–∑ localStorage
 */
export function getFavoriteCities(): FavoriteCity[] {
  if (typeof window === 'undefined') {
    return [];
  }

  const storedData = localStorage.getItem(FAVORITES_STORAGE_KEY);
  if (!storedData) {
    return [];
  }

  try {
    return JSON.parse(storedData);
  } catch (error) {
    console.error('Error parsing favorite cities from localStorage:', error);
    return [];
  }
}

/**
 * –î–æ–±–∞–≤–∏—Ç—å –≥–æ—Ä–æ–¥ –≤ –∏–∑–±—Ä–∞–Ω–Ω–æ–µ
 */
export function addToFavorites(city: Omit<FavoriteCity, 'addedAt'>): FavoriteCity[] {
  const favorites = getFavoriteCities();

  // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –µ—Å—Ç—å –ª–∏ —É–∂–µ —ç—Ç–æ—Ç –≥–æ—Ä–æ–¥ –≤ –∏–∑–±—Ä–∞–Ω–Ω–æ–º
  const exists = favorites.some(fav => fav.id === city.id);

  if (!exists) {
    // –î–æ–±–∞–≤–ª—è–µ–º –Ω–æ–≤—ã–π –≥–æ—Ä–æ–¥ —Å –≤—Ä–µ–º–µ–Ω–Ω–æ–π –º–µ—Ç–∫–æ–π
    const newCity: FavoriteCity = {
      ...city,
      addedAt: Date.now()
    };

    const updatedFavorites = [...favorites, newCity];

    // –°–æ—Ö—Ä–∞–Ω—è–µ–º –≤ localStorage
    if (typeof window !== 'undefined') {
      localStorage.setItem(FAVORITES_STORAGE_KEY, JSON.stringify(updatedFavorites));
    }

    return updatedFavorites;
  }

  return favorites;
}

/**
 * –£–¥–∞–ª–∏—Ç—å –≥–æ—Ä–æ–¥ –∏–∑ –∏–∑–±—Ä–∞–Ω–Ω–æ–≥–æ
 */
export function removeFromFavorites(cityId: string): FavoriteCity[] {
  const favorites = getFavoriteCities();

  // –§–∏–ª—å—Ç—Ä—É–µ–º —Å–ø–∏—Å–æ–∫, –∏—Å–∫–ª—é—á–∞—è –≥–æ—Ä–æ–¥ —Å —É–∫–∞–∑–∞–Ω–Ω—ã–º ID
  const updatedFavorites = favorites.filter(city => city.id !== cityId);

  // –°–æ—Ö—Ä–∞–Ω—è–µ–º –≤ localStorage
  if (typeof window !== 'undefined') {
    localStorage.setItem(FAVORITES_STORAGE_KEY, JSON.stringify(updatedFavorites));
  }

  return updatedFavorites;
}

/**
 * –ü—Ä–æ–≤–µ—Ä–∏—Ç—å, –Ω–∞—Ö–æ–¥–∏—Ç—Å—è –ª–∏ –≥–æ—Ä–æ–¥ –≤ –∏–∑–±—Ä–∞–Ω–Ω–æ–º
 */
export function isCityInFavorites(cityId: string): boolean {
  const favorites = getFavoriteCities();
  return favorites.some(city => city.id === cityId);
}

/**
 * –ü–æ–ª—É—á–∏—Ç—å –∏–∑–±—Ä–∞–Ω–Ω—ã–π –≥–æ—Ä–æ–¥ –ø–æ –µ–≥–æ ID
 */
export function getFavoriteCityById(cityId: string): FavoriteCity | undefined {
  const favorites = getFavoriteCities();
  return favorites.find(city => city.id === cityId);
}

/**
 * –°–æ—Ä—Ç–∏—Ä–æ–≤–∞—Ç—å –∏–∑–±—Ä–∞–Ω–Ω—ã–µ –≥–æ—Ä–æ–¥–∞
 */
export function sortFavoriteCities(
  cities: FavoriteCity[],
  sortBy: 'name' | 'addedAt' = 'addedAt',
  order: 'asc' | 'desc' = 'desc'
): FavoriteCity[] {
  return [...cities].sort((a, b) => {
    if (sortBy === 'name') {
      return order === 'asc'
        ? a.name.localeCompare(b.name)
        : b.name.localeCompare(a.name);
    } else {
      return order === 'asc'
        ? a.addedAt - b.addedAt
        : b.addedAt - a.addedAt;
    }
  });
}

/**
 * –û—á–∏—Å—Ç–∏—Ç—å –≤—Å–µ –∏–∑–±—Ä–∞–Ω–Ω—ã–µ –≥–æ—Ä–æ–¥–∞
 */
export function clearAllFavorites(): void {
  if (typeof window !== 'undefined') {
    localStorage.removeItem(FAVORITES_STORAGE_KEY);
  }
}
